@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600&display=swap);
body{background-color:#E5E5E5;min-height:100%}

.App{display:flex;flex-wrap:wrap;padding:26px 28px;grid-column-gap:32px;-webkit-column-gap:32px;column-gap:32px;grid-row-gap:24px;row-gap:24px;justify-content:center}

.card-item{width:227px;height:240px;border-radius:24px 0 24px 24px;box-shadow:0px 1px 8px 0px rgba(0,0,0,0.1),0px 2px 4px 0px rgba(0,0,0,0.12),0px 1px 2px 0px rgba(0,0,0,0.16);flex-shrink:0;position:relative}.card-image{width:100%;height:164px;overflow:hidden;border-top-left-radius:24px}.card-image img{width:100%;object-fit:cover;height:100%}.card-info{width:100%;height:76px;padding:16px;box-sizing:border-box}.card-star{position:absolute;top:-36px;right:-37px;height:75px;width:75px;background:center/cover no-repeat url(data:image/png;base64,)}.card-title{color:#263238;font-family:Inter;font-style:normal;font-weight:600;font-size:20px;line-height:28px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.card-description{color:#263238;font-family:Inter;font-style:normal;font-weight:normal;font-size:14px;line-height:20px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.card-bottom-section{display:flex;justify-content:space-between}.card-rotate button{position:relative;top:26px;right:54px;width:24px;height:24px;border:0;background:center/cover no-repeat url(data:image/png;base64,);cursor:pointer;transform:scaleX(-1)}

.card-list{display:flex;flex-wrap:wrap;padding:26px 28px;grid-column-gap:32px;-webkit-column-gap:32px;column-gap:32px;grid-row-gap:24px;row-gap:24px;align-content:center;justify-content:center;max-width:800px}

